/* Register.css */
.register-container {
  max-width: 500px;
  margin: 40px auto;
  padding: 25px;
  background: #0f3460; /* Dark blue card background */
  border: 2px solid #4a90e2; /* Blue border */
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  font-family: 'Poppins', sans-serif;
  color: #e0e0e0;
  transition: transform 0.3s ease;
}

body {
  background: linear-gradient(135deg, #1a1a2e, #16213e); /* Full-page dark gradient */
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

.register-container h1 {
  text-align: center;
  color: #ffd700; /* Gold title */
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 2rem;
}

.register-container form {
  display: flex;
  flex-direction: column;
}

.register-container label {
  margin-bottom: 10px;
  color: #b3cde0; /* Light blue labels */
  font-weight: 500;
}

.register-container input {
  width: 100%;
  padding: 10px;
  margin-top: 5px;
  border: 1px solid #4a90e2;
  border-radius: 5px;
  box-sizing: border-box;
  font-size: 14px;
  background: #0f3460; /* Matches card background */
  color: #e0e0e0;
  transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

.register-container input:focus {
  outline: none;
  border-color: #82c4ff;
  box-shadow: 0 0 8px rgba(130, 196, 255, 0.6);
}

.register-container button {
  margin-top: 20px;
  padding: 12px;
  background: linear-gradient(90deg, #ff6f61, #e85d50); /* Coral gradient */
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.3s ease;
}

.register-container button:hover {
  background: linear-gradient(90deg, #e85d50, #cc4b42);
  transform: translateY(-3px);
}

.register-container button:active {
  transform: translateY(0);
}

.register-container .error-message {
  color: #ff6f61;
  text-align: center;
  margin-bottom: 15px;
  font-size: 14px;
}

.register-container .success-message {
  color: #27ae60;
  text-align: center;
  margin-bottom: 15px;
  font-size: 14px;
}

.register-container br {
  display: block;
  margin-bottom: 10px;
}

@media (max-width: 600px) {
  .register-container {
    margin: 20px;
    padding: 15px;
  }

  .register-container h1 {
    font-size: 1.7rem;
  }

  .register-container input {
    font-size: 13px;
  }

  .register-container button {
    padding: 10px;
    font-size: 14px;
  }
}