/* Home.css */
.home-container {
  min-height: 100vh;
  font-family: 'Poppins', sans-serif;
  color: #e0e0e0;
  overflow-x: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
}

.background-video {
  position: fixed; /* Changed from absolute to fixed for full coverage */
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
  opacity: 0.7;
}

.hero-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 60px); /* Adjust for Nav height */
  text-align: center;
  padding: 20px;
  animation: fadeIn 1.5s ease-in-out;
  position: relative; /* Ensures content is above video */
  z-index: 1; /* Places content above video */
}

.hero-section h1 {
  font-size: 3.5rem;
  color: #ffd700; /* Gold title */
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 20px;
  text-shadow: 2px 2px 10px rgba(255, 215, 0, 0.5);
}

.hero-section .subtext {
  font-size: 1.2rem;
  color: #b3cde0;
  max-width: 600px;
  margin-bottom: 30px;
  line-height: 1.6;
  opacity: 0.9;
}

.explore-button {
  padding: 12px 30px;
  font-size: 1.1rem;
  font-weight: bold;
  color: #fff;
  background: linear-gradient(90deg, #4a90e2, #82c4ff); /* Blue gradient */
  border: none;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(74, 144, 226, 0.4);
  text-transform: uppercase;
  letter-spacing: 1px;
}

.explore-button:hover {
  background: linear-gradient(90deg, #82c4ff, #4a90e2);
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 6px 20px rgba(74, 144, 226, 0.6);
}

.explore-button:active {
  transform: translateY(0) scale(1);
}

/* Features Section */
.features-section {
  padding: 40px 20px;
  background: rgba(15, 52, 96, 0.9); /* Semi-transparent dark blue */
  margin-top: -20px; /* Overlap with hero for smooth transition */
  border-top: 1px solid #4a90e2;
  position: relative; /* Ensures content is above video */
  z-index: 1; /* Places content above video */
}

.features-section h2 {
  text-align: center;
  color: #ffd700;
  font-size: 2.5rem;
  margin-bottom: 30px;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.features-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.feature-card {
  background: rgba(255, 255, 255, 0.05); /* Glassmorphism effect */
  backdrop-filter: blur(5px);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.feature-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 15px rgba(74, 144, 226, 0.3);
}

.feature-card h3 {
  color: #4a90e2;
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.feature-card p {
  color: #b3cde0;
  font-size: 1rem;
  line-height: 1.5;
}

/* Stats Section */
.stats-section {
  text-align: center;
  margin-top: 30px;
}

.stats-section h3 {
  color: #ffd700;
  font-size: 1.8rem;
}

.stats-section .user-count {
  font-size: 2.2rem;
  color: #4a90e2;
  animation: countBlink 1s infinite alternate;
}

.db-info {
  color: #4DB33D; /* MongoDB green */
  font-size: 1rem;
  margin-top: 10px;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(77, 179, 61, 0.3);
}

@keyframes countBlink {
  from { opacity: 0.7; }
  to { opacity: 1; }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .hero-section h1 {
    font-size: 2.5rem;
  }

  .hero-section .subtext {
    font-size: 1rem;
  }

  .features-section h2 {
    font-size: 2rem;
  }

  .feature-card h3 {
    font-size: 1.2rem;
  }

  .feature-card p {
    font-size: 0.9rem;
  }

  .stats-section h3 {
    font-size: 1.5rem;
  }

  .stats-section .user-count {
    font-size: 1.8rem;
  }

  .explore-button {
    padding: 10px 20px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .hero-section h1 {
    font-size: 2rem;
  }

  .hero-section .subtext {
    font-size: 0.9rem;
  }

  .features-section h2 {
    font-size: 1.7rem;
  }

  .feature-card {
    padding: 15px;
  }

  .feature-card h3 {
    font-size: 1rem;
  }

  .feature-card p {
    font-size: 0.8rem;
  }

  .stats-section h3 {
    font-size: 1.2rem;
  }

  .stats-section .user-count {
    font-size: 1.5rem;
  }

  .explore-button {
    padding: 8px 15px;
  }
}